@import url(https://fonts.googleapis.com/css2?family=Homemade+Apple&family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dawning+of+a+New+Day&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center;min-height:100vh;overflow:hidden;display:block;position:relative;color:#bdb1a2}
.title-text{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);color:#fff;font-family:"Tamoro";font-size:6vw;font-weight:bold;white-space:nowrap}.Landing{height:100vh;background-image:url(/static/media/landing.a4ddb6a6.jpg);background-size:cover;background-repeat:no-repeat;background-position:50% 50%;text-align:center;position:relative}
.welcome-bgrnd{position:relative;min-height:100vh;display:table;background-color:#5f5d4b;text-align:center;overflow:hidden}.welcome-msg{display:table-cell;vertical-align:middle;padding:10vw;color:#26201d}.welcome-txt{font-size:2.5vh}.welcome-spacing{height:5vh}
@font-face{font-family:Hello-Paris;src:url("/static/media/Hello Paris Sans Regular.18f25d4b.ttf") format("truetype");font-weight:normal;font-style:normal}@font-face{font-family:Tamoro;src:url(/static/media/TamoroScript.429eb5ae.ttf) format("truetype");font-weight:normal;font-style:normal}body{font-family:Lato,Tahoma,serif}.hdr-msg{font-family:Hello-Paris;font-size:7vh}.about-bgrnd{position:relative;min-height:100vh;display:table;background-color:#5f5d4b;text-align:center}.about-outer{display:table-cell;vertical-align:middle;height:80vh}.about-page{color:#f1efe8;font-family:"Lato"}.left-image{max-height:70vh;max-width:33vw}.left-block{visibility:hidden;max-height:0px;vertical-align:middle;width:100%}.right-block{display:table-cell;vertical-align:middle;padding:30px}.hwm-block{padding:10px}.hwm-header{font-size:5vh;width:20%;vertical-align:middle;text-align:left;font-family:Hello-Paris}.ln-brk{width:60vw;border-top:3px solid #26201d}@media only screen and (orientation: landscape){.about-page{display:flex}.left-block{visibility:visible;max-height:100%;width:33vw}.right-block{width:67vw;padding-right:10vw}}
.footer-container{height:10vh;position:relative;bottom:0;width:100%;padding:12px 0 0px 5vh;background-color:#bdb1a2}.copy-txt{color:#fff}
.accomodations-bgrnd{position:relative;min-height:100vh;display:table;background-color:#f1efe8;text-align:center}.accomodations-page{display:table-cell;vertical-align:middle;font-family:"Lato"}.accomodations-msg{vertical-align:middle;padding-top:10vw;padding-bottom:10vw;color:#26201d}.accomodations-txt{font-size:2.5vh;padding-bottom:5vh}.accomodations-spacing{height:5vh}.acm-header{font-size:4vh;text-decoration:underline}@media only screen and (orientation: landscape){.accomodations-page{display:flex}.accomodations-msg{width:50%}}
.schedule-bgrnd{position:relative;min-height:100vh;display:table;background-color:#f1efe8;text-align:center;overflow:hidden}.ln-brk{width:90vw;border-top:3px solid #26201d;margin:auto;background-color:#f1efe8}.schedule-msg{display:table-cell;vertical-align:middle;padding-top:10vw;padding-bottom:10vw;color:#26201d;object-fit:cover}.schedule-txt{font-size:2.5vh;padding-bottom:5vh}.schedule-spacing{height:10vh}
.rsvp-bgrnd{position:relative;min-height:100vh;display:table;background-color:#5f5d4b;text-align:center;overflow:hidden}.rsvp-msg{padding:5vmin;display:table-cell;vertical-align:middle;color:#26201d}.rsvp-form{font-size:2.5vh}.iform-frame{width:600px;height:600px;margin:0 auto}
.photos-bgrnd{position:relative;height:100vh;background-color:#bdb1a2;vertical-align:middle;display:table}.outer-page{display:table-cell;vertical-align:middle}.photos-page{vertical-align:middle;padding:3vw}@media only screen and (orientation: landscape){.photos-page{display:flex}}.sbs-image{object-fit:cover}
.navbar{text-align:center;height:5vh;background-color:rgba(255,255,255,.7)}
